html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-main);
  line-height: var(--LH-main);
}

h1,
h2,
h3 {
  margin-top: 0;
  color: var(--limegreen);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --toggle: hsl(163, 72%, 41%);
}

:root {
  --font-main: "Open Sans", sans-serif;
  --LH-main: 160%;
}

.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle);
}